import React, { useState } from "react";

interface TicketProps {
  id: number;
  title: string;
  priority: string;
  status: string;
  descripition: string;
}

interface ModalProps {
  ticket: TicketProps;
  onClose: () => void;
}

const ModalTicketInfo: React.FC<ModalProps> = ({ ticket, onClose }) => {
  return (
    <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
      <div className="bg-white p-4 rounded shadow-lg">
        <h2 className="text-xl font-bold mb-4">{ticket.title}</h2>
        <p>
          <strong>ID:</strong> {ticket.id}
        </p>
        <p>
          <strong>Priority:</strong> {ticket.priority}
        </p>
        <p>
          <strong>Status:</strong> {ticket.status}
        </p>
        <p>
          <strong>Description:</strong> {ticket.descripition}
        </p>
        <button
          onClick={onClose}
          className="mt-4 px-4 py-2 bg-blue-500 text-white rounded"
        >
          Close
        </button>
      </div>
    </div>
  );
};

export const TicketInfoRow: React.FC<TicketProps> = ({
  id,
  title,
  priority,
  status,
  descripition,
}) => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleRowClick = () => {
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  return (
    <>
      <tr
        onClick={handleRowClick}
        key={id}
        className="hover:bg-blue-500 hover:text-white transition-all cursor-pointer"
      >
        <td className="border border-gray-300 text-center w-20 px-4 py-2">
          {id}
        </td>
        <td className="border border-gray-300  px-4 py-2">{title}</td>
        <td className="border border-gray-300 w-30 px-4 py-2">{priority}</td>
        <td className="border border-gray-300 text-center w-40 px-4 py-2">
          {status === "open" ? (
            <span className="inline-block w-2 h-2 mr-2 bg-green-500 rounded-full"></span>
          ) : (
            <span className="inline-block w-2 h-2 mr-2 bg-red-500 rounded-full"></span>
          )}
          {status}
        </td>
      </tr>
      {isModalOpen && (
        <ModalTicketInfo
          ticket={{ id, title, priority, status, descripition }}
          onClose={handleCloseModal}
        />
      )}
    </>
  );
};
